export function formConfig() {
  return [
    {
      label:"",
      type: 'input',
      model: 'name',
      placeholder: '',
      'suffix-icon':"el-icon-date",
      rules: [{ required: true, message: '', trigger: 'blur' }],
      slot: () => (
        {
          prepend: [<div>A</div>]
        }
      )
    },
    {
      label:"",
      type: 'input',
      model: 'age',
      placeholder: '',
      rules: [{ required: true, message: '', trigger: 'blur' }]
    },
    {
      label:"",
      type: 'input',
      model: 'age3',
      placeholder: '',
      rules: [{ required: true, message: '', trigger: 'blur' }],
    },
  ]
}
