import Props from '../compositionApi/props/AttrProps'
const Provide = { template: '<div>Provide</div>' }

/* /  */
export default {
    name: 'vue-api',
    path: 'vue-api',
    component: { template: ' <router-view></router-view>'},
    children: [
        {
            path: 'props',
            name: 'PropAttribute',
            component: Props
        },
        {
            path: 'provide',
            name: 'Provide/Inject',
            component: Provide,
        }
    ]
}